.c-tinder-card {
  background: #696969;
  color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px;

  img {
    max-width: 250px;
  }

  &__section {
    padding: 16px;
  }

  &__title {
    padding: 16px;
  }

  &__img {
    margin: 32px;
    max-width: 250px;

    & > img,
    & > svg {
      width: 100%;
    }
  }

  &__btns {
    width: 100%;
    padding: 32px;
    display: flex;
    justify-content: space-between;
  }
}