body{
    background: #696969;
    display: flex;
    justify-content: center;
}

.container {
    background-image: url("../assets/images/dog.png");
    background-repeat:no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    width: 100vw;
    padding: 20px 0px;
}

h1.title {
    font: helvetica;
    color: #673300;
    font-size: 46px;
    padding-bottom: 15px;
    padding: 0;
    margin: 0;
    align-items: flex-end;
}
h2.title {
    font: helvetica;
    color: #673300;
    font-size: 20px;
    text-shadow: none;
    padding-bottom: 15px;
    padding: 0;
    margin: 0;
}


h2 {
    font: helvetica;
    color: #fff;
    text-shadow: 2px 2px #5C5D5D;
    font-size: 30px;
    padding-bottom: 15px;
}

.button-group {
    padding-bottom: 60px;
}
.btn {
    background: #fbfbfb;
    background: linear-gradient(to bottom, #fbfbfb 0%, #f2f4f7 100%);
    border: none;
    color: white;
    padding: 12px 16px;
    font-size: 16px;
    cursor: pointer;
    width: 280px;
    color: #5C5D5D;
    text-align: center;
    border-radius: 5px;
    border: 2px solid #a9a9a9;    
    margin-bottom: 10px;
    display: inline-block;

    input {
        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
    
    p {
        margin: 0px;
        padding: 4px 0px 0px 10px;
        font-size: 24px;
        display: inline-flex;
        min-width: 170px;
    }
    
}